.home-container {
    position: relative;
    min-height: 100vh;
    padding: 2rem;
    color: white;
    z-index: 1;
  }
  
  .home-container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: url("./public/background.jpg") no-repeat center center/cover; */
    opacity: 0.4; /* sheer effect */
    z-index: -2;
  }
  
  .background-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: white;
    opacity: 0.5; /* makes text stand out */
    z-index: -1;
  }
  
  .contact-section {
    text-align: center;
    margin-top: 50px;
  }
  
  .contact-icons {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 15px;
  }
  
  .contact-icons a {
    color: black; /* Adjust depending on background */
    transition: transform 0.2s, color 0.3s;
  }
  
  .contact-icons a:hover {
    color: #0077b5; /* LinkedIn blue, or pick a hover color */
    transform: scale(1.2);
  }
  